import { responsiveMediaAbove } from "../settings/responsive";
import { CLASS_ACTIVE, CLASS_INITIALISED } from "../tools/constants/classes";

// Selectors
const SELECTOR_SCROLLER = '[data-articles-scroller]';

document.querySelectorAll<HTMLElement>(SELECTOR_SCROLLER).forEach(element => {
  if(element.classList.contains(CLASS_INITIALISED)) return;
  element.classList.add(CLASS_INITIALISED);

  let isDown = false;
  let startX:number;
  let scrollLeft:number;

  element.addEventListener('mousedown', (e) => {
    if(responsiveMediaAbove('MEDIUM')) return;

    isDown = true;
    startX = e.pageX - element.offsetLeft;
    scrollLeft = element.scrollLeft;
  });

  element.addEventListener('mouseleave', (e) => {
    if(responsiveMediaAbove('MEDIUM')) return;

    isDown = false;
    element.classList.remove(CLASS_ACTIVE);
  });

  element.addEventListener('mouseup', (e) => {
    isDown = false;
    element.classList.remove(CLASS_ACTIVE);
  });

  element.addEventListener('mousemove', (e) => {
    if(responsiveMediaAbove('MEDIUM')) return;

    if(!isDown) return;
    element.classList.add(CLASS_ACTIVE);
    e.preventDefault();
    const x = e.pageX - element.offsetLeft;
    const walk = (x - startX) * 3;
    element.scrollLeft = scrollLeft - walk;
  });
});
